import React, { useRef } from 'react';
import ReactDOM from 'react-dom/client';
import { Helmet } from 'react-helmet'
import '../index.css';
//import App from './App';
//import Interact from './txt2imgIn'
import reportWebVitals from '../reportWebVitals';
import Background from '../imgs/sakura.png';
import { motion, useViewportScroll, useTransform, useScroll } from 'framer-motion'
import workBackground  from '../imgs/castle.png'

export default function WorkSection(){
  const parallax = React.useRef(null);

  React.useEffect(() => {
    function scrollHandler() {
      const element = parallax.current;
      if (element) {
        let offset = window.pageYOffset;
        element.style.backgroundPositionY = offset * .12 + 'px';
      }
    }
    window.addEventListener('scroll', scrollHandler)
    // return the function to remove the handler
    // which will be run whn Hero is unmounted
    return () => window.removeEventListener('scroll', scrollHandler);
  }, []);

  return(
    <div style={{
    height: '600px',
    boxShadow: '0px 0px 2px',
    backgroundImage: 'url(' + workBackground + ')',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center',
    backgroundSize: 'cover',
    backgroundAttachment: 'fixed',
    padding: '2rem',
    fontFamily: 'Source Code Pro',
    display: 'flex',
    flexDirection: 'column'
    
    }} 
    ref={parallax}>
      <div style={{height: '400px', width: '600px', position: 'relative', backgroundColor: 'rgba(240, 240, 240, 0.5)', borderRadius: '10px', margin: '2rem', padding: '2rem'}}>
        <h1 style={{margin:'2rem', position: 'relative'}}>Work and Projects</h1>
        <p style={{position: 'absolute', bottom: '5%', left: '0', marginTop: 'auto', margin:'2rem', lineHeight: '2rem'}}>Throughout my years as a programmer I have worked on many different projects, both small and large, but here I will list some of my most noteable projects I made while I was learning. Two are swift apps for iOS devices, and another is a unity game mostly based in C#. I will most likely be adding to this list over time, but these few are what I have listed now. Click the button to the right to see all the works in full.</p>
      </div>
    </div>
  )
}