function FrontPage() 
{
  const parallax = React.useRef(null);
  React.useEffect(() => {
    function scrollHandler() {
      const element = parallax.current;
      if (element) {
        let offset = window.pageYOffset;
        element.style.backgroundPositionY = -offset * .12 + 'px';
      }
    }
    window.addEventListener('scroll', scrollHandler)
    // return the function to remove the handler
    // which will be run whn Hero is unmounted
    return () => window.removeEventListener('scroll', scrollHandler);
    }, []);

    return(
      <motion.div className="divBack" ref={parallax} style={{
        backgroundImage: 'url(' + Background + ')', 
        backgroundPosition: 'center',
        backgroundSize: 'cover',
        backgroundAttachment: 'fixed',
        backgroundSize: ''
      }}>
          
          <NavBar />
          
          <div className="MainText" style={{
            height: '800px',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'center',
            textAlign: 'center',
            fontFamily: 'Source Code Pro',
          }}>
            <h1 style={{ color: 'black', fontSize: '4rem', paddingTop: '10vh' }}>Noah McCampbell</h1>
            <h1 style={{ color: 'black', fontSize: '2rem', fontWeight:'normal' }}>17 Year Old Web, Game, and Software Developer; Video Editor; CTF Competitor.</h1>
            
            <h1 style={{ color: 'black', fontSize: '2rem', fontWeight:'normal', paddingTop: '10rem', paddingBottom : '0px' }}>|</h1>
            <h1 style={{ color: 'black', fontSize: '2rem', fontWeight:'normal', paddingTop: '0px' }}>V</h1>
            
          </div>
          
          
          <Experience />

          <WorkSection />
          
      </motion.div>
      
    );
}

